@-webkit-keyframes jiggle {
  0% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
  }
  25% {
    -webkit-transform: translateX(10%) translateY(-10%);
            transform: translateX(10%) translateY(-10%);
  }
  75% {
    -webkit-transform: translateX(-10%) translateY(10%);
            transform: translateX(-10%) translateY(10%);
  }
  100% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
  }
}
@keyframes jiggle {
  0% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
  }
  25% {
    -webkit-transform: translateX(10%) translateY(-10%);
            transform: translateX(10%) translateY(-10%);
  }
  75% {
    -webkit-transform: translateX(-10%) translateY(10%);
            transform: translateX(-10%) translateY(10%);
  }
  100% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
  }
}
@-webkit-keyframes wiggle {
  0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  25% {
    -webkit-transform: rotateZ(-20deg);
            transform: rotateZ(-20deg);
  }
  50% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  75% {
    -webkit-transform: rotateZ(20deg);
            transform: rotateZ(20deg);
  }
  100% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}
@keyframes wiggle {
  0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  25% {
    -webkit-transform: rotateZ(-20deg);
            transform: rotateZ(-20deg);
  }
  50% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  75% {
    -webkit-transform: rotateZ(20deg);
            transform: rotateZ(20deg);
  }
  100% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}
@-webkit-keyframes yflip {
  from {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}
@keyframes yflip {
  from {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}
@-webkit-keyframes zspin {
  from {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}
@keyframes zspin {
  from {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}
@-webkit-keyframes dropin {
  from {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes dropin {
  from {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

body, html {
  margin: 0;
  padding: 0;
}
#app, body {
  background-color: black;
}
#app {
  text-shadow: 0 0 9px black;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Shadows Into Light', cursive;
  text-align: center;
  color: #705588;
  z-index: 0;
  position: relative;
  top: 0;
  left: 0;
  width: calc(100vw - 20px);
  height: calc(100vh - 20px);
  margin: 0;
  padding: 10px;
  overflow-x: visible;
  overflow-y: auto;
}
#app > *:not(.backgroundHolder):not(.spotlight) {
    -webkit-animation-name: dropin;
            animation-name: dropin;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}
#background {
  min-width: 100vw;
  min-height: 100vh;
}
.backgroundHolder {
  z-index: -100;
  position:fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  -webkit-filter: brightness(30%) blur(5px);
          filter: brightness(30%) blur(5px);
  max-width: 100vw;
  max-height: 100vh;
  overflow: visible;
}

.title {
  margin: 0;
  color: #e14ae8;
  background: -webkit-linear-gradient(#c36b6b, #ebafff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(225, 74, 232, 0.4);
  /*box-shadow: -10px 7px 20px 14px white;*/
  font-size: 4em;
  background-color: rgba(71, 97, 113, 0.11);
  font-family: 'Permanent Marker', cursive;
}
p {
  font-size: 3em;
}
.description {
  margin: 0 10%;
}
.description .language, .description .stack {
  color: #eeeffd;
}
.description span {
  display: inline-block;
}
.spotlight {
  -webkit-box-shadow: 0 0 20px 14px rgba(0,0,0,0.5);
          box-shadow: 0 0 20px 14px rgba(0,0,0,0.5);
  z-index: -1;
  position: fixed;
  bottom: -100px;
  display: inline-block;
  border: 100px solid rgba(0,0,0,0.5);
  border-width: 700px 60px;
  border-radius: 50%  / 60%   60%  40%  40%;
}
.name {
  margin: 0;
  font-size: 2em;
  margin-bottom: 30px;
  color: #a1b1e0;
}
.spotlight.left {
  left: -30px;
}
.spotlight.right {
  right: -30px;
}
.display {
  position: relative;
  margin-top: 25px;
  display: inline-block;
  -webkit-filter: invert(50%) grayscale(100) sepia(100%) brightness(140%) hue-rotate(250deg);
          filter: invert(50%) grayscale(100) sepia(100%) brightness(140%) hue-rotate(250deg);
}
.display div.row {
  display: inline-block;
}
.display img {
  max-width: 200px;
  max-height: 200px;
  margin: 25px 50px;
  -webkit-filter: drop-shadow(0 0 5px black);
          filter: drop-shadow(0 0 5px black);
}
.display .ruby {
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8);
  -webkit-filter: drop-shadow(0 0 3px black);
          filter: drop-shadow(0 0 3px black);
}
.display .nodejs {
  -webkit-transform: scale(1.6);
      -ms-transform: scale(1.6);
          transform: scale(1.6);
}
.display .python {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-filter: drop-shadow(0 0 3px black);
          filter: drop-shadow(0 0 3px black);
}
div.wiggle, div.jiggle, div.yflip, div.zspin {
  display: inline-block;
}
div.wiggle:
{
  -webkit-animation: wiggle 0.2s linear;
          animation: wiggle 0.2s linear;
}
div.jiggle:hover {
  -webkit-animation: jiggle 0.4s linear;
          animation: jiggle 0.4s linear;
}
div.yflip:hover {
  -webkit-animation: yflip 0.7s linear;
          animation: yflip 0.7s linear;
}
div.zspin:hover {
  -webkit-animation: zspin 0.7s linear;
          animation: zspin 0.7s linear;
}
.github {
  margin: 0;
}
.mern {
  font-size: 1.2em;
}

.eye {
  position: absolute;
  opacity: 0;
  top: 50%;
  border-radius: 100px;
  border: 5px solid white;
  -webkit-box-shadow: 0 0 10px white;
          box-shadow: 0 0 10px white;
  -webkit-transition: opacity 0.3s linear;
  -o-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
  -webkit-box-shadow: 0 0 6px 2px white;
          box-shadow: 0 0 6px 2px white;
}
.eye.left {
  left: 40%;
  -webkit-transition: opacity 0.3s linear, left 1s ease, top 1s ease !important;
  -o-transition: opacity 0.3s linear, left 1s ease, top 1s ease !important;
  transition: opacity 0.3s linear, left 1s ease, top 1s ease !important;
}
.eye.right {
  right: 40%;
  -webkit-transition: opacity 0.3s linear, right 1s ease, top 1s ease !important;
  -o-transition: opacity 0.3s linear, right 1s ease, top 1s ease !important;
  transition: opacity 0.3s linear, right 1s ease, top 1s ease !important;
}
/* .gitdisplay:hover .eye {
  opacity: 1;
  transition: opacity 0.05s linear;
} */
.githublink {
  color: white;
  text-decoration: none;
}
.downarrow {
  display: inline;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid white;
  -webkit-transition: border-top-color 0.1s linear;
  -o-transition: border-top-color 0.1s linear;
  transition: border-top-color 0.1s linear;
}
.githublink:hover .downarrow {
  border-top-color: #a1b1e0;
  -webkit-transition: border-top-color 0.3s linear;
  -o-transition: border-top-color 0.3s linear;
  transition: border-top-color 0.3s linear;
}
.githublink h1 {
  -webkit-transition: color 0.1s linear;
  -o-transition: color 0.1s linear;
  transition: color 0.1s linear;
}
.githublink:hover h1 {
  color: #a1b1e0;
  -webkit-transition: color 0.3s linear;
  -o-transition: color 0.3s linear;
  transition: color 0.3s linear;
}



